// src/components/dashboard/HistorySection.tsx
"use client";

import React, { useEffect, useState } from "react";
import { Table, Button, OverlayTrigger, Tooltip, Spinner, Alert } from "react-bootstrap";
import { Edit3 } from "react-feather";

// Tipado de la reserva
interface Reservation {
  id: string;
  date: string; // ISO string
  service: { name: string };
  therapist: { name: string };
}

export default function HistorySection() {
  const [reservations, setReservations] = useState<Reservation[]>([]);
  const [loading, setLoading] = useState(true);
  const [editedOnceId, setEditedOnceId] = useState<string | null>(null);

  useEffect(() => {
    fetch("/api/appointments")
      .then((res) => {
        if (!res.ok) throw new Error("No autorizado");
        return res.json();
      })
      .then((data) => setReservations(data.reservations || []))
      .catch(console.error)
      .finally(() => setLoading(false));
  }, []);

  if (loading) {
    return (
      <div className="text-center py-5">
        <Spinner animation="border" />
      </div>
    );
  }

  if (reservations.length === 0) {
    return <Alert variant="info" className="text-center">A√∫n no tienes reservas en tu historial.</Alert>;
  }

  const handleEdit = (resv: Reservation) => {
    if (editedOnceId === resv.id) return;
    // aqu√≠ ir√≠a la l√≥gica para mostrar un modal o formulario de edici√≥n
    // tras guardar, seteas:
    setEditedOnceId(resv.id);
  };

  return (
    <div className="overflow-auto">
      <Table hover responsive className="dashboard-table">
        <thead>
          <tr>
            <th>Servicio</th>
            <th>Terapeuta</th>
            <th>Fecha y hora</th>
            <th>Acciones</th>
          </tr>
        </thead>
        <tbody>
          {reservations.map((r) => {
            const dt = new Date(r.date);
            const fechaStr = dt.toLocaleDateString();
            const horaStr = dt.toLocaleTimeString([], { hour: "2-digit", minute: "2-digit" });
            const canEdit = editedOnceId !== r.id && dt > new Date();
            return (
              <tr key={r.id}>
                <td>{r.service.name}</td>
                <td>{r.therapist.name}</td>
                <td>{`${fechaStr} ‚Ä¢ ${horaStr}`}</td>
                <td>
                  <OverlayTrigger
                    placement="top"
                    overlay={
                      <Tooltip>
                        {canEdit
                          ? "Editar reserva"
                          : "S√≥lo se puede editar una √∫nica ocasi√≥n"}
                      </Tooltip>
                    }
                  >
                    <span className={canEdit ? "cursor-pointer" : "text-muted"}>
                      <Edit3
                        size={20}
                        onClick={() => canEdit && handleEdit(r)}
                        style={{ cursor: canEdit ? "pointer" : "not-allowed" }}
                      />
                    </span>
                  </OverlayTrigger>
                </td>
              </tr>
            );
          })}
        </tbody>
      </Table>
    </div>
  );
}